video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}
.video-overlay {
  position: fixed;
  right: 50;
  bottom: 50;
  min-width: 100%;
  min-height: 100%;
  margin: 10px;
  padding: 5px 5px;
  font-size: 40px;
  font-family: Helvetica;
  color: #fff;
  background-color: rgba(50, 50, 50, 0.3);
}
div {
  white-space: pre-wrap;
}
.button {
  height: 50px;
  width: 200px;
  font-size: 20px;
  font-weight: bold;
}
.header {
  text-align: left;
  font-size: 50px;
  font-family: Helvetica;
  color: #fff;
}
.stat {
  text-align: center;
}
